import styles from "./checkInfluencer.module.css";

const CheckInfluencer = ({ name, addAuthor, deleteAuthor }) => {
  const onCheckBox = (event) => {
    if (event.target.checked) {
      return addAuthor(event.target.name);
    }
    deleteAuthor(event.target.name);
  };
  return (
    <div className={styles.checkbox}>
      <input
        className={styles.input}
        type="checkbox"
        id={name}
        name={name}
        onChange={onCheckBox}
      />
      <label className={styles.label} htmlFor={name}>
        {name}
      </label>
    </div>
  );
};

export default CheckInfluencer;
