import React, { useState } from "react";
import styles from "./admin.module.css";

const Admin = ({ database }) => {
  const [nftObj, setNftObj] = useState();
  console.log(nftObj);
  const onChangeHandler = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setNftObj({ ...nftObj, [name]: value });
  };

  const onSubmitHandler = (event) => {
    event.preventDefault();
    const { id, name, desc, src, likeCount, contractAddress, tokenId } = nftObj;
    database.writeNftData(
      id,
      name,
      desc,
      src,
      likeCount,
      contractAddress,
      tokenId
    );
  };
  return (
    <div className={styles.container}>
      <form onSubmit={onSubmitHandler}>
        <legend>Register Gallery</legend>
        <div className={styles.id}>
          <label htmlFor="id">id</label>
          <input type="text" id="id" name="id" onChange={onChangeHandler} />
        </div>
        <div className={styles.contractAddress}>
          <label htmlFor="contractAddress">contractAddress</label>
          <input
            type="text"
            id="contractAddress"
            name="contractAddress"
            onChange={onChangeHandler}
          />
        </div>
        <div className={styles.desc}>
          <label htmlFor="desc">desc</label>
          <input type="text" id="desc" name="desc" onChange={onChangeHandler} />
        </div>
        <div className={styles.likeCount}>
          <label htmlFor="likeCount">likeCount</label>
          <input
            type="text"
            id="likeCount"
            name="likeCount"
            onChange={onChangeHandler}
          />
        </div>
        <div className={styles.name}>
          <label htmlFor="name">name</label>
          <input type="text" id="name" name="name" onChange={onChangeHandler} />
        </div>
        <div className={styles.src}>
          <label htmlFor="src">src</label>
          <input type="text" id="src" name="src" onChange={onChangeHandler} />
        </div>
        <div className={styles.tokenId}>
          <label htmlFor="tokenId">tokenId</label>
          <input
            type="text"
            id="tokenId"
            name="tokenId"
            onChange={onChangeHandler}
          />
        </div>
        <button type="submit">write</button>
      </form>
    </div>
  );
};

export default Admin;
